<template>
  <el-card class="box-card">
    <div class="header" slot="header">
      <span>友情链接</span>
      <el-button @click="insert_dialog_visible = true" plain size="mini" type="primary"
                 icon="el-icon-circle-plus-outline">
        添加链接
      </el-button>
    </div>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="id" label="#" width="40">
      </el-table-column>
      <el-table-column prop="title" label="链接标题">
      </el-table-column>
      <el-table-column prop="website" label="网站地址">
        <template slot-scope="scope">
          <el-link href="#" target="_blank">{{ scope.row.website }}</el-link>
        </template>
      </el-table-column>
      <el-table-column prop="create_time" label="添加日期">
      </el-table-column>
      <el-table-column label="操作" width="180">
        <template slot-scope="scope">
          <el-button size="mini" icon="el-icon-edit" type="primary" plain>编辑</el-button>
          <el-button size="mini" icon="el-icon-delete" type="danger" plain>删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加链接Dialog -->
    <el-dialog title="添加友情链接" :visible.sync="insert_dialog_visible">
      <el-form label-width="80px">
        <el-form-item label="链接标题">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="链接地址">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="跳转方式">
          <el-radio label="_blank">新窗口跳转</el-radio>
          <el-radio label="_self">本窗口跳转</el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
				<el-button @click="insert_dialog_visible = false">取 消</el-button>
				<el-button type="primary" @click="insert_dialog_visible = false">确 定</el-button>
			</span>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  name: "List",
  data() {
    return {
      tableData: [{
        "id": 3,
        "title": "window之家",
        "website": "http://www.baidu.com",
        "create_date": "2022-03-17T15:40:14.000Z",
        "update_date": null,
        "create_time": "2022-03-17 23:40:14",
      }],
      insert_dialog_visible: false,
    }
  },
  created() {
    document.title = "友情链接列表";
  },

}
</script>

<style lang="less" scoped>
.box-card {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
</style>